import React, { Component } from 'react';
import { Breadcrumb, BreadcrumbItem, Button, Label, Col, Row } from 'reactstrap';
import { Link } from 'react-router-dom';
import { Control, Form, Errors } from 'react-redux-form';

const required = (val) => val && val.length;
const maxLength = (len) => (val) => !(val) || (val.length<=len);
const minLength = (len) => (val) => (val) && (val.length>=len) ;
const isNumber = (val) => !isNaN(Number(val));
const validEmail = (val) => /^[A-Z0-9._+%-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(val);
class Contact extends Component {
    constructor(props) {
        super(props);
        this.handleSubmit=this.handleSubmit.bind(this);
    }
    handleSubmit(values) {
        // console.log('Current State is: ' + JSON.stringify(values));
        // alert('Current State is: ' + JSON.stringify(values));
        this.props.resetFeedbackForm();
        this.props.postFeedback(values);
        // event.preventDefault();
    }
    render() {
        return (
            <div className="container">
                        <div className="row">
                            <Breadcrumb>
                                <BreadcrumbItem><Link to="/home">Home</Link></BreadcrumbItem>
                                <BreadcrumbItem active>Contact Us</BreadcrumbItem>
                            </Breadcrumb>
                            <div className="col-12">
                                <h3>Contact Us</h3>
                                <hr />
                            </div>
                        </div>
                        <div className="row row-content">
                        <div className="col-12">
                            <h3>Location Information</h3>
                        </div>
                            <div className="col-12 col-sm-4 offset-sm-1">
                                <h5>Our Address</h5>
                                    <address>
                                    121, Clear Water Bay Road<br />
                                    Clear Water Bay, Kowloon<br />
                                    HONG KONG<br />
                                    <i className="fa fa-phone"></i>: +852 1234 5678<br></br>
                                    <i className="fa fa-fax"></i>: +852 8765 4321<br></br>
                                    <i className="fa fa-envelope"></i>:
                                        <a href="mailto:confusion@food.net">confusion@food.net</a>
                                </address>
                            </div>
                            <div className="col-12 col-sm-6 offset-sm-1">
                                <h5>Map of our Location</h5>
                            </div>
                            <div className="col-12 col-sm-11 offset-sm-1">
                                <div className="btn-group" role="group">
                                    <a className="btn btn-primary" role="button" href="tel:+852 8765 4321"><i class="fa fa-phone fa-lg"></i>Call</a>
                                    <a className="btn btn-info" role="button" href="."><i class="fa fa-skype fa-lg"></i>Skype</a>
                                    <a className="btn btn-success" role="button" href="mailto:confusion@food.net"><i class="fa fa-envelope-o fa-lg"></i>Email</a>
                                </div>
                            </div>
                        </div>
                        <div className="row row-content">
                            <div className="col-12">
                                <h3>Send us your Feedback</h3>
                            </div>
                            <div className="col-12 col-md-9">
                                <Form model="feedback" onSubmit={(values) => this.handleSubmit(values)}>
                                    <Row className="form-group">
                                        <Label htmlFor="firstname" md={3}>First Name</Label>
                                        <Col md={9}>
                                            <Control.text model=".firstname" id="firstname" name="firstname" placeholder="First Name" className="form-control" validators={{required,minLength:minLength(3),maxLength:maxLength(10)}}/>
                                        <Errors className="text-danger" model=".firstname" show="touched" messages={{required:' Required',minLength:' Length must be greater than or equal to 3 characters',maxLength:' Length must be lesser than or equal to 10 characters'}} />
                                        </Col>
                                    </Row>
                                    <Row className="form-group">
                                        <Label htmlFor="lastname" md={3}>Last Name</Label>
                                        <Col md={9}>
                                            <Control.text model=".lastname" id="lastname" name="lastname" placeholder="Last Name" className="form-control" validators={{required,minLength:minLength(3),maxLength:maxLength(10)}}/>
                                        <Errors className="text-danger" model=".lastname" show="touched" messages={{required:' Required',minLength:' Length must be greater than or equal to 3 characters',maxLength:' Length must be lesser than or equal to 10 characters'}} />
                                        </Col>
                                    </Row>   
                                    <Row className="form-group">
                                        <Label htmlFor="telnum" md={3}>Contact Tel.</Label>
                                        <Col md={9}>
                                            <Control.text model=".telnum" id="telnum" name="telnum" placeholder="Tel. number" className="form-control" validators={{required,minLength:minLength(8),maxLength:maxLength(10),isNumber}}/>
                                        <Errors className="text-danger" model=".telnum" show="touched" messages={{required:' Required',minLength:' Length must be greater than or equal to 8 digits',maxLength:' Length must be lesser than or equal to 13 digits',isNumber:' Input must be a Number'}} />
                                        </Col>
                                    </Row>
                                    <Row className="form-group">
                                        <Label htmlFor="email" md={3}>Email</Label>
                                        <Col md={9}>
                                            <Control.text model=".email" id="email" name="email" placeholder="E-mail" className="form-control" validators={{required,validEmail}}/>
                                        <Errors className="text-danger" model=".email" show="touched" messages={{required:' Required',validEmail:' Invalid E-mail Address'}} />
                                        </Col>
                                    </Row>
                                    <Row className="form-group">
                                        <Col md={{size: 5, offset: 3}}>
                                            <div className="form-check">
                                                <Label check>
                                                    <Control.checkbox model=".agree" name="agree" className="form-check-input" /> {' '}<strong>May we Contact you?</strong>                                            
                                                </Label>
                                            </div>
                                        </Col>
                                        <Col md={{size: 3, offset: 1}}>
                                            <Control.select model=".contactType" name="contactType" className="form-control">                                            
                                                <option>Tel.</option>
                                                <option>Email</option>
                                            </Control.select>
                                        </Col>
                                    </Row>
                                    <Row className="form-group">
                                        <Label htmlFor="message" md={3}>Your Feedback</Label>
                                        <Col md={9}>
                                            <Control.textarea model=".message" id="message" name="message" rows="12" className="form-control"/>
                                        </Col>
                                    </Row>
                                    <Row className="form-group">
                                        <Col md={{size:9, offset:3}}>
                                            <Button type="submit" color="primary">Send Feedback</Button>
                                        </Col>
                                    </Row>
                                    </Form>
                            </div>
                    </div>
            </div>
        );
    }
}
export default Contact;