import React, { Component } from 'react';
import { Card, CardBody, CardImg, CardTitle, CardText, Button, Breadcrumb, BreadcrumbItem, Modal, ModalHeader, ModalBody, Label, Row } from 'reactstrap';
import { Link } from 'react-router-dom';
import { Control, LocalForm, Errors } from 'react-redux-form';
import { Loading } from './LoadingComponent';   
import { baseUrl } from '../shared/baseUrl';
import { FadeTransform, Stagger, Fade } from 'react-animation-components';

function RenderDish({dish}) {
    return(
        
        <div className="col-12 col-md-5 mx-5">
            <FadeTransform in transformProps={{exitTransform:'scale(0.5) translateY(-50%) translateX(0%)'}}>
                <Card>
                    <CardImg src={baseUrl+dish.image} alt={dish.name}></CardImg>
                    <CardBody>
                        <h4><CardTitle>{dish.name}</CardTitle></h4>
                        <CardText>{dish.description}</CardText>
                    </CardBody>
                </Card>                
            </FadeTransform>
        </div>  
    );
}
const required = (val) => val && val.length;
const maxLength = (len) => (val) => !(val) || (val.length<=len);
const minLength = (len) => (val) => (val) && (val.length>=len) ;
class CommentForm extends Component {
    constructor(props){
        super(props);
        this.toggleModal=this.toggleModal.bind(this);
        this.handleSubmit=this.handleSubmit.bind(this);
        this.state = {
            isModalOpen: false
        };
    }
    handleSubmit(values) {
        this.toggleModal();
        this.props.postComment(this.props.dishId, values.rating, values.name, values.comment);
    }
    toggleModal() {
        this.setState({
            isModalOpen: !this.state.isModalOpen
        });
    }
    render () {
        return (
            <div>
                <Button outline onClick={this.toggleModal}>
                    <i className="fa fa-pencil"></i> Submit Comment
                </Button>
                <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                    <ModalHeader toggle={this.toggleModal}>Submit Comment</ModalHeader>
                    <ModalBody>
                        <LocalForm onSubmit={(values) => this.handleSubmit(values)}>
                            <Row className="form-group m-1 pt-2">
                                <Label htmlFor="ratings"><h6>Rating</h6></Label>
                                <Control.select className="form-control" model=".ratings" name="ratings" id="ratings">
                                    <option>1</option>
                                    <option>2</option>
                                    <option>3</option>
                                    <option>4</option>
                                    <option>5</option>
                                </Control.select>                        
                            </Row>
                            <Row className="form-group m-1 pt-2">
                                <Label htmlFor="name"><h6>Your Name</h6></Label>
                                <Control.text model=".name" id="name" name="name" placeholder="Your Name" className="form-control" validators={{required,minLength:minLength(3),maxLength:maxLength(15)}}/>
                                <Errors className="text-danger" model=".name" show="touched" messages={{required:' Required',minLength:' Length must be greater than or equal to 3 characters',maxLength:' Length must be lesser than or equal to 15 characters'}} />           
                            </Row>
                            <Row className="form-group m-1 pt-2">
                                <Label htmlFor="Comment" ><h6>Comment</h6></Label>
                                <Control.textarea model=".comment" id="comment" name="comment" rows="6" className="form-control"/>
                            </Row>
                            <Row className="form-group m-1 pt-2">
                                <Button type="submit" color="primary">Send Feedback</Button>
                            </Row>
                        </LocalForm>
                    </ModalBody>
                </Modal>
            </div>
        );
    }
}
function RenderComments({comment, postComment, dishId}) {
    const comments = comment.map((Comment) => {
        return (
           <Fade in>
                <li className="list-unstyled" key={Comment.id}>
                    <p>{Comment.comment}</p>
                    <p>-- {Comment.author}, {new Intl.DateTimeFormat('en-US', { year: 'numeric', month: 'short', day: '2-digit'}).format(new Date(Date.parse(Comment.date)))}</p>
                </li>
            </Fade> 
        );
    });
    return(
        <div className="col-12 col-md-5 px-5">
            <h4>Comments</h4>
            <Stagger in>
                {comments}
            </Stagger> 
            <CommentForm postComment={postComment} dishId={dishId} />    
        </div>
    )
}
const Dishdetail = (props) => {      
    if (props.isLoading) {
        return(
            <div className="container">
                <div className="row">            
                    <Loading />
                </div>
            </div>
        );
    }
    else if (props.errMess) {
        return(
            <div className="container">
                <div className="row">            
                    <h4>{props.errMess}</h4>
                </div>
            </div>
        );
    }
    else if (props.dish != null) {
        return (
            <div className="container">
                <div className="row">
                    <Breadcrumb>
                        <BreadcrumbItem><Link to="/home">Home</Link></BreadcrumbItem>
                        <BreadcrumbItem><Link to="/menu">Menu</Link></BreadcrumbItem>
                        <BreadcrumbItem active>{props.dish.name}</BreadcrumbItem>
                    </Breadcrumb>
                    <div className="col-12">
                        <h3>{props.dish.name}</h3>
                        <hr />
                    </div>
                </div>
                <div className="row d-flex">
                    <RenderDish dish={props.dish}/>
                    <RenderComments comment={props.comments} postComment={props.postComment} dishId={props.dish.id} />
    
                </div>
            </div>
        );
    }
    
    else {
        return(
            <div></div>
        );
    }
}

export default Dishdetail;